





















import Component from 'vue-class-component';
import VueBase from '@/utils/VueBase';
import { dataManager } from '@/utils/DataManager';

@Component({
  components: {
  },
})
export default class extends VueBase {
  public get dataManager() {
    return dataManager;
  }

  get eKind() {
    return this.$store.state.enemiesFilter.eKind || null;
  }

  set eKind(value) {
    this.$store.commit('enemiesFilter/setEKind', value);
  }

  public get name() {
    return this.$store.state.enemiesFilter.name;
  }

  public set name(value) {
    this.$store.commit('enemiesFilter/setName', value);
  }

  public get sort() {
    return this.$store.state.enemiesFilter.sort;
  }

  public set sort(value) {
    this.$store.commit('enemiesFilter/setSort', value);
  }

  public get enemyCategoryFilter() {
    return dataManager.enemy.KindList
      .filter((p) => p.iKind)
      .map((p) => ({
        label: p.strName,
        value: p.iKind,
      }));
  }

  public get sortOptions() {
    return [
      {
        label: this.$t('古い'),
        value: 0,
      },
      {
        label: this.$t('新しい'),
        value: 1,
      },
    ];
  }

  public get enemies() {
    return this.eKind ? (dataManager.enemiesByEKind[this.eKind] || []) : dataManager.enemiesOrderByCategory;
  }

  public get filteredEnemies() {
    const enemies = this.enemies.filter((p) => (
      (!this.name || p.strName.toLocaleLowerCase().includes(this.name.toLocaleLowerCase()))
    ));
    if (this.sort === 1) {
      return enemies.reverse();
    }
    return enemies;
  }
}
